export default {
  layout: "coreLayout",
  data() {  
    debugger;
    return {
      message:"",
      username: '',
      password: '' 
    };
  }, 
  methods: {
    async login(e) { 
      e.preventDefault();

      //$('#loading-modal').modal('show');  
      
      const payload = {
        username: this.username,
        password: this.password
      };
      try {
        await this.$auth.loginWith('local', {
          data: payload
        });
        //$('#loading-modal').modal('hide') 
        this.message = 'valid';
        this.$router.push('/admin/news'); 
      } catch (e) { 
        //$('#loading-modal').modal('hide')
        this.message = 'invalid';
        //this.$router.push('/login');
      }
    }
  }
};
